import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { AppConfig, TransactionConfig, urljoin } from '../app.config';
import { Observable } from 'rxjs';

@Injectable()
export class TransactionService {

    private transactionConfig: TransactionConfig;

    constructor(protected config: AppConfig, protected http: HttpClient) {
        this.transactionConfig = this.config.getTransactionConfig();
    }

    public getDefaultConfig(): string {
        return this.transactionConfig.defaultConfig;
    }

    public getDefaultRepository(): string {
        return this.transactionConfig.defaultRepository;
    }

    public getOauthAuthorizeUrl(): string {
        return this.transactionConfig.oauthAuthorize;
    }

    public getUser(): Observable<Object> {
        const accessToken = localStorage.getItem('access_token');
        const url = urljoin(this.transactionConfig.apiUser, '?access_token=' + accessToken);
        return this.http.get(url);
    }

    public getConfigTree(): Observable<Array<Object>> {
        const accessToken = localStorage.getItem('access_token');
        const httpHeaders = new HttpHeaders()
            .set('Accept', 'application/json');
        const httpParams = new HttpParams()
            .set('access_token', accessToken)
            .set('path', 'config');
        return this.http.get<Array<Object>>(this.transactionConfig.apiTree, {headers: httpHeaders, params: httpParams});
    }

    public getFile(filepath): Observable<Object> {
        const accessToken = localStorage.getItem('access_token');
        const httpHeaders = new HttpHeaders()
            .set('Accept', 'application/json');
        const httpParams = new HttpParams()
            .set('access_token', accessToken)
            .set('ref', this.transactionConfig.branch);

        const url = urljoin(this.transactionConfig.apiFiles, encodeURIComponent(filepath), 'raw');
        return this.http.get(url, {headers: httpHeaders, params: httpParams});
    }

    public getResultTree(): Observable<Array<Object>> {
        const accessToken = localStorage.getItem('access_token');
        const httpHeaders = new HttpHeaders()
            .set('Accept', 'application/json');
        const httpParams = new HttpParams()
            .set('access_token', accessToken);
        return this.http.get<Array<Object>>(this.transactionConfig.apiResultTree,
            {headers: httpHeaders, params: httpParams});
    }

    public getResultFile(filepath): Observable<Object> {
        const accessToken = localStorage.getItem('access_token');
        const httpHeaders = new HttpHeaders()
            .set('Accept', 'application/json');
        const httpParams = new HttpParams()
            .set('access_token', accessToken)
            .set('ref', this.transactionConfig.branch);

        const url = urljoin(this.transactionConfig.apiResultFiles, encodeURIComponent(filepath), 'raw');
        return this.http.get(url, {headers: httpHeaders, params: httpParams});
    }
}
