import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class AppConfig {
    private static readonly CONFIG_JSON = 'assets/transaction.config.json';
    private transactionConfig: TransactionConfig;

    constructor(private http: HttpClient) {
    }

    public getTransactionConfig() {
        return this.transactionConfig;
    }

    public loadConfig() {
        // TransactionConfig
        this.http.get<TransactionConfig>(AppConfig.CONFIG_JSON).subscribe(data => {
            const config = <TransactionConfig> {
                // Basic
                gitlab : String(data['gitlab']) || '',
                projectId: String(data['project_id']) || '',
                branch: data['branch']  || '',
                // Oauth
                appId: data['appid'] || '',
                appSecret: data['appsecret']  || '',
                callbackUrl: data['callback_url']  || '',
            };

            // Get the access_token by this url
            config.oauthAuthorize = urljoin(config.gitlab, 'oauth/authorize',
                '?client_id=' + config.appId,
                '&client_secret=' + config.appSecret,
                '&redirect_uri=' + config.callbackUrl,
                '&response_type=token');

            // APIs. All APIs needs access_token
            config.api = urljoin(config.gitlab, data['api']['version']);

            config.apiUser = urljoin(config.api, data['api']['user']);
            config.apiFiles = urljoin(config.api,
                'projects', config.projectId, data['api']['files']);
            config.apiTree = urljoin(config.api,
                'projects', config.projectId, data['api']['tree']);

            // Defaults
            config.defaultConfig = data['defaultConfig'];
            config.defaultRepository = data['defaultRepository'];

            // Result
            config.projectResultId = String(data['project_result_id']) || '';
            config.apiResultFiles = urljoin(config.api,
                'projects', config.projectResultId, data['api']['files']);
            config.apiResultTree = urljoin(config.api,
                'projects', config.projectResultId, data['api']['tree']);

            this.transactionConfig = config;

            console.info('AppConfig loaded');
        });
    }
};

export interface TransactionConfig {

    // Basic
    gitlab: string,
    projectId: string,       // See http://gitlab.meizu.com/sys/automerger
    branch: string,

    // Oauth
    appId: string,
    appSecret: string,
    callbackUrl: string,
    oauthAuthorize: string,

    // APIs
    api: string,
    apiUser: string,
    apiFiles: string,
    apiTree: string,

    // Defaults
    defaultConfig: string,
    defaultRepository: string,

    // AutoMerger Result
    projectResultId: string, // See http://gitlab.meizu.com/sys/automerger-result
    apiResultTree: string,
    apiResultFiles: string,
}

export function urljoin(...args: string[]) {
    const strArray = [].slice.call(args);
    const resultArray = [];

    // If the first part is a plain protocol, we combine it with the next part.
    if (strArray[0].match(/^[^/:]+:\/*$/) && strArray.length > 1) {
        const first = strArray.shift();
        strArray[0] = first + strArray[0];
    }

    // There must be two or three slashes in the file protocol, two slashes in anything else.
    if (strArray[0].match(/^file:\/\/\//)) {
        strArray[0] = strArray[0].replace(/^([^/:]+):\/*/, '$1:///');
    } else {
        strArray[0] = strArray[0].replace(/^([^/:]+):\/*/, '$1://');
    }

    for (let i = 0; i < strArray.length; i++) {
        let component = strArray[i];

        if (typeof component !== 'string') {
            throw new TypeError('Url must be a string. Received ' + component);
        }

        if (component === '') { continue; }

        if (i > 0) {
            // Removing the starting slashes for each component but the first.
            component = component.replace(/^[\/]+/, '');
        }
        if (i < strArray.length - 1) {
            // Removing the ending slashes for each component but the last.
            component = component.replace(/[\/]+$/, '');
        } else {
            // For the last component we will combine multiple slashes to a single one.
            component = component.replace(/[\/]+$/, '/');
        }

        resultArray.push(component);

    }

    let str = resultArray.join('/');
    // Each input component is now separated by a single slash except the possible first plain protocol part.

    // remove trailing slash before parameters or hash
    str = str.replace(/\/(\?|&|#[^!])/g, '$1');

    // replace ? in parameters with &
    const parts = str.split('?');
    str = parts.shift() + (parts.length > 0 ? '?' : '') + parts.join('&');

    return str;
}
